<template>
  <van-popup
    :show="modelValue"
    position="center"
    teleport="body"
    :round="true"
    @click-overlay="onClose"
  >
    <Column class="p-40 w-650">
      <span class="font-40 m-b-28">{{ $t('vote.projectProfile') }}</span>
      <Column v-loading="loading" class="label-lg w-100b lh-44 h-620">
        {{ content }}
      </Column>
      <el-button type="primary" class="h-80 w-100b label-lg bold radius-8" @click="onClose">{{ $t('vote.gotIt') }}</el-button>
    </Column>
  </van-popup>
</template>
<script setup>

const emit = defineEmits()
const props = defineProps({
  modelValue: {
    default: false
  },
  itemId: {},
})

let content = $ref('')
let loading = $ref(false)

const onClose = () => {
  emit('update:modelValue', false)
}

const getData = async () => {
  try {
    loading = true
    const body = {
      itemId: props.itemId,
    }
    const d = await voteApi.projectProfile(body)
    content = d.introduction
  } finally {
    loading = false
  }
}

onBeforeMount(() => {
  // getData()
})

watch(() => props.modelValue, (show) => {
  if (show) {
    getData()
  } else {
    content = ''
  }
})

</script>

