<template>
  <Column class="VoteList ">
    <Row v-if="!isEmpty(data.activityId)" class="w-100b m-t-30 m-b-20 " x="sb" y="e">
      <Column class="f-size-370 w-370">
        <div class="label bold m-b-6">{{ data.title }}</div>
        <span class="label-sm c-info">
          <span>{{ dayjs(data.startTime).format('YYYY.MM.DD') }}</span>
          <span class="m-lr-2">-</span>
          <span>{{ dayjs(data.endTime).format('YYYY.MM.DD') }}</span>
        </span>
      </Column>
      <Row :class="languageStore.locale == LANGEN? '':''">
        <el-switch v-model="type" active-value="1" :size="'small'" inactive-value="0" />
        <span class="m-l-10 label-sm">{{ $t('vote.showVotesCast') }}</span>
      </Row>
    </Row>
    <ListEmpty v-if="isEmpty(list)" class="w-100b" x="c" />
    <Column v-if="!isEmpty(list)" class="w-100b">
      <VoteItem
        v-for="(row) in list.filter(e => e.index <= 3)"
        :key="row.index"
        :class="topColors[row.index].class"
        :border="topColors[row.index].border"
        :color="topColors[row.index].color"
        :bottomBorder="bottomColors[row.index].border"
        :data="data"
        :row="row"
        @onRefresh="getData"
      >
      <!-- VoteItem -->
      </VoteItem>
      <VoteItem
        v-for="(row) in list.filter(e => e.index > 3)"
        :key="row.index"
        :preamble="row.index"
        :data="data"
        :row="row"
        @onRefresh="getData"
      />
    </Column>
  </Column>
</template>
<script setup>
import { VoteItem, VoteList } from '@/view/vote/components'
const languageStore = useLanguageStore()

const emit = defineEmits()
const props = defineProps({
})

const topColors = reactive({
  1: { color: '#F6D361', bg: 'linear-gradient( 180deg, #F6D361 0%, #FFFFFF 100%)', class: 'top-1-bg', border: 'rgba(246,211,97)' },
  2: { color: '#AAD5EF', bg: 'linear-gradient( 180deg, #AAD5EF 0%, #FFFFFF 100%)', class: 'top-2-bg', border: 'rgba(169,212,238)' },
  3: { color: '#D59D6B', bg: 'linear-gradient( 180deg, #D59D6B 0%, #FFFFFF 100%)', class: 'top-3-bg', border: 'rgba(213,157,107)' },
})

const bottomColors = reactive({
  1: { color: '#F6D361', bg: 'linear-gradient( 180deg, #F6D361 0%, #FFFFFF 100%)', class: 'top-1-bg', border: 'rgba(246,211,97,0.5)' },
  2: { color: '#AAD5EF', bg: 'linear-gradient( 180deg, #AAD5EF 0%, #FFFFFF 100%)', class: 'top-2-bg', border: 'rgba(169,212,238,0.5)' },
  3: { color: '#D59D6B', bg: 'linear-gradient( 180deg, #D59D6B 0%, #FFFFFF 100%)', class: 'top-3-bg', border: 'rgba(213,157,107,0.5)' },
})
let data = $ref({})
let list = $ref([])
const type = $ref(0)

const getData = async () => {
  const body = { type: type }
  const d = await voteApi.list(body)
  console.log('--voteApi.list--', d)
  data = d
  list = d.childrenPage ?? []
  // list = d.childrenPage?.map((e, i) => ({ ...e, index: i + 1 })) ?? []
}

watch(() => type, () => {
  getData()
})

onBeforeMount(() => {
  getData()
})
</script>

