<template>
  <Column
    class="VoteItem w-100b  radius-16 m-b-30 position-r"
    :style="{ 'border': `1px solid ${border}`}"
  >
    <Row v-if="!isEmpty(preamble)" class="preamble bg-line label-lg c-info bold position-a top-30 right-45" x="c" y="c">{{ preamble }}</Row>
    <div v-else class="prize" />
    <Row class="w-100b h-180 p-lr-30 p-tb-40 ooooo" :style="{'border-bottom': `1px solid ${bottomBorder}`}" x="s" y="c">
      <img :src="row.iconUrl" class="h-72 w-72 m-r-18">
      <Column class=" " y="sb">
        <span class="font-54 bold">{{ row.coinName }}</span>
        <Row class="label-sm c-info">
          <span class="m-l-3" @click="() => toProjectProfile(row)">{{ $t('vote.projectProfile') }} </span>
          <div class="w-1 h-24 bg-placeholder-50 m-lr-20" />
          <Row @click="() => winMes.postParent('openBrowser', { url: row.whitePaper })">
            <IconSvg class=" " name="icon-a-zu14364" />
            <span class="m-l-14">  {{ $t('public.whitePaper') }} </span>
          </Row>
          <div class="w-1 h-24 bg-placeholder-50 m-lr-20" />
          <Row @click="() => winMes.postParent('openBrowser', { url: row.officialWebsite })">
            <IconSvg class=" " name="icon-a-zu14363" />
            <span class="m-l-14">  {{ $t('public.officialWebsite') }} </span>
          </Row>
        </row>
      </Column>
    </Row>
    <Column class="w-100b p-t-60 p-lr-20" x="c">
      <span class="label-sm c-info m-b-12">{{ $t('vote.totalNumberVotes') }}(BFT)</span>
      <Num class="font-54 bold c-primary m-b-48">{{ row.totalVoteAmount ?? 0 }}</Num>
      <Row class="w-100b label-sm m-b-16" x="sb">
        <span class="c-info">{{ $t('vote.voted') }}(BFT)</span>
        <Num class="bold">{{ row.voteAmount ?? 0 }}</Num>
      </Row>
      <Row class="w-100b label-sm m-b-40" x="sb">
        <span class="c-info">{{ $t('vote.totalPrizePool') }}({{ row.coinName }})</span>
        <Num class="bold">{{ row.awardAmount ?? 0 }}</Num>
      </Row>
      <el-button class="w-100b c-white bold label-lg h-70 radius-42 m-b-42" :color="color" @click="() => onVote(row)">{{ $t('vote.vote') }}</el-button>
    </Column>
    <DiaProjectProfile v-model="projectProfileData.show" :itemId="projectProfileData.itemId" />
    <DiaVote
      v-model="diaVoteData.show"
      :activityId="diaVoteData.activityId"
      :itemId="diaVoteData.itemId"
      :coinName="diaVoteData.coinName"
      :limitAmount="diaVoteData.limitAmount"
      :holdAmount="diaVoteData.holdAmount"
      :lowestAmount="diaVoteData.lowestAmount"
      :data="diaVoteData.data"
      @submited="submited"
    />
    <DiaHasNotBegun v-model="diaHasNotBegunData.show" />
  </Column>
</template>

<script setup>
import { DiaProjectProfile, DiaVote, DiaHasNotBegun } from '@/view/vote/components'

const emit = defineEmits()
const props = defineProps({
  border: {
    default: '#DBDDE9',
  },
  color: {
    default: '#2649EF',
  },
  bottomBorder: {
    default: '#dbdde97a',
  },
  preamble: {},
  data: {},
  row: {},
})

const userStore = useUserStore()

const projectProfileData = reactive({
  show: false,
  itemId: ''
})

const diaHasNotBegunData = reactive({
  show: false,
})

const diaVoteData = reactive({
  show: false,
  activityId: '',
  itemId: '',
  coinName: '',
  limitAmount: '',
  holdAmount: '',
  lowestAmount: '',
  data: ''
})

const toProjectProfile = (item) => {
  projectProfileData.show = true
  projectProfileData.itemId = item.itemId
}

const submited = () => {
  emit('onRefresh')
}

const onVote = (item) => {
  if (!userStore.token) {
    winMes.postParent('toLogin')
    // winMes.postParent('toLogin', { url: window.location.href })
    return
  }
  if (props.data.status) {
    diaVoteData.show = true
    diaVoteData.activityId = props.data.activityId
    diaVoteData.itemId = props.data.itemId
    diaVoteData.coinName = props.data.coinName
    diaVoteData.limitAmount = props.data.limitAmount
    diaVoteData.holdAmount = props.data.holdAmount
    diaVoteData.lowestAmount = props.data.lowestAmount
    diaVoteData.data = props.row
  } else {
    diaHasNotBegunData.show = true
  }
}

onBeforeMount(() => {
})
onMounted(() => {})
</script>

<style lang="scss">
.VoteItem {
  .preamble{
    width: 55px;
    height: 55px;
    border-radius: 50%;
  }
 }
</style>
