<template>
  <van-popup
    :show="modelValue"
    position="bottom"
    teleport="body"
    :round="false"
    @click-overlay="onClose"
  >
    <Column class="w-100b bg-white">
      <Row class="w-100b h-110 p-lr-30 bg-line" y="c" x="sb">
        <Row>
          <img :src="data.iconUrl" class="w-44 h-44 m-r-14">
          <span class="title-sm bold">{{ data.coinName }}</span>
        </Row>
        <img class="w-24 h-24" src="@/image/vote/x.webp" alt="" @click="onClose">
      </Row>
      <Column class="w-100b p-lr-30 p-t-40 p-b-80">
        <Column class="w-100b bg-line  label p-30 m-b-30">
          <Row class="w-100b m-b-30" x="sb">
            <span class="c-info">{{ $t('vote.numberVotesCast') }}</span>
            <Row class="bold">
              <Num class=" m-r-4">{{ data.voteAmount ?? 0 }}</Num>
              <span>BFT</span>
            </Row>
          </Row>
          <Row class="w-100b m-b-30" x="sb">
            <span class="c-info">{{ $t('vote.individualVotingLimit') }}</span>
            <Row class="bold">
              <Num class=" m-r-4">{{ limitAmount ?? 0 }}</Num>
              <span>BFT</span>
            </Row>
          </Row>
          <Row class="w-100b " x="sb">
            <span class="c-info">{{ $t('vote.totalNumberVotesProjects') }}</span>
            <Row class="bold">
              <Num class=" m-r-4">{{ data.totalVoteAmount ?? 0 }}</Num>
              <span>BFT</span>
            </Row>
          </Row>
        </Column>
        <span class="label-sm bold m-b-10">{{ $t('vote.numberVotes') }}</span>
        <el-input :modelValue="num" class="m-b-6" :placeholder="`${$t('vote.minimumNumberVotes')} ${lowestAmount}`" @input="numChange">
          <template #suffix>
            <span class="label c-info">BFT</span>
            <span class="w-1 h-12 bg-placeholder m-lr-10" />
            <el-button class="label p-0" type="primary" link @click="toMax"><span class="label">{{ $t('trade.max') }}</span></el-button>
          </template>
        </el-input>
        <Row class="label-sm ">
          <span class="m-r-10 c-info">{{ $t('trade.available', 'trade.quantity') }}</span>
          <span class="m-r-10 bold">
            <Num class="m-r-4">{{ BFTamount }}</Num>
            <span>BFT</span>
          </span>
        <!-- <el-button class="label-sm h-18 bold p-0" type="primary" link @click="transfer">{{ $t('wallet.transfer') }}</el-button> -->
        </Row>
        <Column class="w-100b m-t-60" x="e">
          <Row class="w-100b">
            <el-checkbox v-model="readAndAccept" size="large" test-key="agreeterms">
              <Row class="f-wrap">
                <span class="label-sm c-d m-r-4">{{ $t("form.readAndAccept2") }}</span>
                <span class="c-primary label-sm" @click="toLink">{{
                  $t("vote.votingCoinageAgreement")
                }}</span>
              </Row>
            </el-checkbox>
          </Row>
          <el-button
            class="w-100b h-88 label-lg"
            type="primary"
            :loading="loading"
            :disabled="!readAndAccept"
            @click="onVote"
          >
            <span>{{ $t('vote.voteNow') }}</span>
          </el-button>
        </Column>
      </Column>
    </Column>
  </van-popup>
</template>
<script setup>
import DiaOverlayJs from '@/components/widget/DiaOverlay'

const userStore = useUserStore()
const languageStore = useLanguageStore()

const emit = defineEmits()
const props = defineProps({
  modelValue: {
    default: false
  },
  activityId: {},
  itemId: {},
  coinName: {},
  limitAmount: {},
  holdAmount: {},
  lowestAmount: {},
  data: {},
})

let loading = $ref(false)
const readAndAccept = $ref(false)

let num = $ref('')

const numChange = (value) => {
  num = value.replace(/[^0-9]/g, '')
}

const BFTamount = $computed(() => userStore?.accountInfo['1']?.coinAccountList?.find(e => e.coinName == 'BFT')?.amount ?? 0)

const toMax = () => {
  const limitAmount = Math.max(props.limitAmount - props.data.voteAmount, 0)
  num = Math.min(limitAmount, props.holdAmount, BFTamount)
}
const toLink = () => {
  const localeMap = {
    [LANGEN]: 'en-001',
    [LANGCN]: 'zh-cn',
    [LANGTW]: 'zh-tw'
  }
  const url = `https://bifinance.zendesk.com/hc/${localeMap[languageStore.locale]}/articles/9591831060623`
  winMes.postParent('openBrowser', { url: url })
}

const onVote = async () => {
  if (!readAndAccept) {
    DiaOverlayJs({ message: $t('form.readPl', 'form.readAndAccept2') })
    // showNotify({ type: 'danger', message: $t('form.readPl', 'form.readAndAccept') })
    return
  }
  if (num < props.lowestAmount) {
    DiaOverlayJs({ message: $t(`${$t('vote.minimumNumberVotes')} ${props.lowestAmount}`) })
    // showNotify({ type: 'danger', message: $t(`${$t('vote.minimumNumberVotes')} ${props.lowestAmount}`) })
    return
  }
  try {
    loading = true
    const body = {
      activityId: props.activityId,
      itemId: props.data.itemId,
      coinName: props.data.coinName,
      iconUrl: props.iconUrl,
      voteAmount: num
    }
    await voteApi.vote(body)
    onClose()
    emit('submited')
  } finally {
    loading = false
  }
}

const onClose = () => {
  emit('update:modelValue', false)
}

onBeforeMount(() => {
  // getData()
})

watch(() => props.modelValue, (show) => {
  if (show) {
    userStore.getAccountInfo()
  } else {
    num = ''
  }
})

</script>

