    </Row>
    <Column v-for="(row) in list" :key="row" class="w-100b">
      <Row class="w-100b" x="s">
        <span class="label bold m-b-20">{{ row.title }}</span>
      </Row>
      <Column class="w-100b p-lr-30 p-t-30 p-b-56 radius-16 m-b-50 position-r border">
        <Row class="w-100b label-sm c-info m-b-30" x="s">
          <span>{{ dayjs(row.startTime).format('YYYY-MM-DD') }}</span>
          <span class="p-lr-4">-</span>
          <span>{{ dayjs(row.endTime).format('YYYY-MM-DD') }}</span>
        </Row>
        <Row class="w-100b">
          <img :src="row.iconUrl" class="w-72 h-72 m-r-20">
          <Column>
            <span class="font-54 bold">{{ row.coinName }}</span>
            <Row>
              <span class="label-sm c-info " @click="() => toProjectProfile(row)">{{ $t('vote.projectProfile') }}</span>
              <div class="w-1 h-24 bg-placeholder-50 m-lr-20" />
              <Row class="label-sm c-info" @click="() => winMes.postParent('openBrowser', { url: row.officialWebsite })">
                <IconSvg class="m-r-14" name="icon-a-zu14364" />
                <span class="">  {{ $t('public.whitePaper') }} </span>
              </Row>
              <div class="w-1 h-24 bg-placeholder-50 m-lr-20" />
              <Row class="label-sm c-info" @click="() => winMes.postParent('openBrowser', { url: row.officialWebsite })">
                <IconSvg class="m-r-14" name="icon-a-zu14363" />
                <span class="">  {{ $t('public.officialWebsite') }} </span>
              </Row>
            </Row>
          </Column>
        </Row>
        <Column class="w-100b  m-t-46 m-b-40" x="c">
          <Num class="font-54 c-primary m-b-4 bold">{{ row.awardAmount ?? 0 }}</Num>
          <span class="label-sm c-info">{{ $t('vote.totalPrizePool') }}({{ row.coinName }})</span>
        </Column>
        <Row class="w-100b  m-b-16" x="sb">
          <span class="label-sm c-info">{{ $t('vote.totalNumberParticipants') }}</span>
