        </Row>
        <Row class="w-100b  m-b-16" x="sb">
          <span class="label-sm c-info">{{ $t('vote.totalNumberVotes') }}(BFT)</span>
          <Num class="label-sm bold">{{ row.totalVoteAmount ?? 0 }}</Num>
        </Row>
        <Row class="w-100b  m-b-16" x="sb">
          <span class="label-sm c-info">{{ $t('vote.totalNumberProjects') }}</span>
          <Num class="label-sm bold">{{ row.itemNumber ?? 0 }}</Num>
        </Row>
        <Row class="w-100b  m-b-42" x="sb">
          <span class="label-sm c-info">{{ $t('vote.totalDestruction') }}(BFT)</span>
