<script setup>
import { DiaProjectProfile } from '@/view/vote/components'

const userStore = useUserStore()
const route = useRoute()

const data = $ref({})
let list = $ref([])

const projectProfileData = reactive({
  show: false,
  itemId: ''
})

const toProjectProfile = (item) => {
  projectProfileData.show = true
  projectProfileData.itemId = item.itemId
}

const getData = async () => {
  const body = { }
  const d = await voteApi.endList(body)
  // data = d
  console.log('--voteApi.endList--', d)
  list = d
}

const open = (url) => {
  window.open(url, '_blank')
}

const onMyVote = (item) => {
  if (!userStore.token) {
    winMes.postParent('toLogin')
    // winMes.postParent('toLogin', { url: window.location.href })
    return
  }
  console.log('onMyVote', item)
  const query = reserveUrlQuery(route)
  router.push({
    path: '/myVote',
    query: {
      ...query,
      activityId: item.activityId
    },
  })
}

onBeforeMount(() => {
  getData()
})
onMounted(() => {})
</script>

